* {
    margin: 0;
    padding: 0;
}

@font-face {
    font-family: 'MontserratBold', sans-serif;
    src: url('/fonts/Montserrat-Bold.ttf') format('woff2');
}

@font-face {
    font-family: 'MontserratRegular', sans-serif;
    src: url('/fonts/Montserrat-Regular.ttf') format('woff2');
}

body {
    width: 100%;
    height: 100vh;
    font-family: 'MontserratRegular', sans-serif;
}

#page {
    width: 100%;
    height: auto;
    margin: 0 auto;
}

ul {
    list-style: none;
}

.container {
    width: 96.6%;
    max-width: 1160px;
    padding: 10px 20px;
    margin: 0 auto;
    display: block;
    border: 1px solid gray;
}

.row {
    width: 100%;
    height: auto;
    display: flex;
    gap: 30px;
    border: 1px solid gray;
}

.column {
    width: 100%;
    height: auto;
    display: block;
    border: 1px solid gray;
}

.col-12 {
    width: 100%;
    height: auto;
    display: block;
    border: 1px solid gray;
}

.col-6 {
    width: 50%;
    max-width: 600px;
    height: auto;
    display: block;
    border: 1px solid gray;
}

.col-4 {
    width: 40%;
    max-width: 400px;
    height: auto;
    display: block;
    border: 1px solid gray;
}

.col-3 {
    width: 30%;
    max-width: 300px;
    height: auto;
    display: block;
    border: 1px solid gray;
}

.col-8 {
    width: 80%;
    max-width: 800px;
    height: auto;
    display: block;
    border: 1px solid gray;
}

